.header{
    display: flex;
justify-content: space-between;   
 border: 1px solid black;
}

.logo{
    width:200px;
}


.nav-items{
    padding: 0px 20px;
}

.nav-items>ul{
    font-size: 24px;
    display: flex;
    list-style-type:none;
   


}

.nav-items>ul>li{
    padding: 10px;
    margin: 10px;
}

.res-container{
    display: flex;
    flex-wrap: wrap;
     
}

.res-card{

    padding: 5px;
    width: 200px;
    margin: 10px;

    



}
.res-card:hover{
    cursor: pointer;
    border: 1px solid black;
}

.search{
    padding: 10px;
    
}

.res-logo{

    width: 100%;
}

.filter-btn{
    margin: 10px;
    cursor: pointer;
}

.shimmer-container{
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card{
    margin: 20px;
    width: 200px;
    height: 400px;
    background-color:#f0f0f0;
}

.login{
    padding: 0 20px;
    cursor: pointer;
}

.filter{
    display: flex;
    
}